<script>
import { onMount } from "svelte";
import dayjs from 'dayjs'
import { apiData, months } from './store.js';

onMount(async () => {
  fetch("https://sea-turtle-app-c25ch.ondigitalocean.app/")
  .then(response => response.json())
  .then(data => {
    apiData.set(data);
  }).catch(error => {
    console.log("error");
    return [];
  });
});
</script>

